<template>
	<view class="my-card_container">
		<cu-custom bgColor="bg-gradual-green" :isBack="true">
			<view slot="backText">返回</view>
			<view slot="content">我的贴子</view>
		</cu-custom>
		<scroll-view scroll-x class="bg-white nav fixed" :style="[{top:customBar + 'px'}]">
			<view class="flex text-center">
				<view :class="['cu-item', 'flex-1', {'text-green cur': index == curTab}]" v-for="(item, index) in menu" :key="index" @click="tabSelect" :data-id="index">{{ item.label }}</view>
			</view>
		</scroll-view>
		<view>
			<Card v-for="(item, index) in list" :key="index" :item="item" :source="1" :status="curTab"></Card>
		</view>
	</view>
</template>

<script>
	import Card from '@/components/card.vue';
	export default {
		components: {
			Card
		},
		data() {
			return {
				customBar: this.CustomBar,
				list: [], // 帖子列表
				menu: [{
					label: '我发起的'
				}, {
					label: '我参与的'
				}, {
					label: '草稿箱'
				}],
				curTab: 0,
				scrollLeft:0,
			}
		},
		created() {
			new Array(10).fill(0).map((v, i) => this.list.push({
				id: i,
				title: '帖子' + i,
				info: '我是帖子介绍，介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍介绍',
				address: '望京soho' + i,
				price: 300 + i
			}));
		},
		methods: {
			tabSelect(e) {
				this.curTab = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			}
		}
	}
</script>

<style lang="scss" lang="scss">
	.my-card_container {
		padding-top: 90upx;
	}
</style>
